<template>
    <div class="IconAbout">
        <div class="imggroups">
            <template v-for="index in imgurl.length">
                <li :key="index">
                    <div class="item">
                        <img :src="imgurl[index-1]">
                    </div>
                    <div class="text">
                        <p>{{content[index-1]}}</p>
                    </div>
                </li>
            </template>
        </div>
    </div>
</template>

<style scoped lang="less">
    .IconAbout {
        .imggroups {

            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-content: center;
            padding: 0.5rem 1.5rem;
            li {
                box-sizing: border-box;
                display: flex;
                margin: 1rem 0;
           
                flex-basis: 48%;
                flex-wrap: wrap;

                .item {
                    flex-basis: 100%;
                    padding: 1rem 0;
                         border: 0.1rem solid #e5e5e5;
                    img {
                        display: block;
                        margin: 0 auto;
                        max-width: 50%;
                    }
                }
                .text {
                    flex-basis: 100%;
                    box-sizing: border-box;
                    padding: 0.8rem 0;
                    font-size: 1.2rem;
                    letter-spacing: 0.1rem;
                    background: #e5e5e5;

                    p {
                        text-align: center;
                    }
                }
            }
        }
    }
</style>
<script>
    export default {
        data() {
            return {
                Message: this.$route.params.result,
                imgurl: [],
                content: []
            }
        },
        created() {
            this.Message.forEach(element => {
                if (element.title === this.$route.params.title) {
                    element.data.img.forEach(x => {
                        this.imgurl.push(x)
                    });
                    element.data.text.forEach(y => {
                        this.content.push(y)
                    })
                }
            });
        },
    }
</script>